/* Ejercicio 7 */
.ej7
{
    display: grid; 
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    gap: 10px;
    width: 100%;
    height: 300px;
    background-color: azure;

    div 
    {
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        background-color: rgb(106, 124, 136);
        color: white;

        &:nth-child(1) {
            grid-column-start: 2;
            grid-column-end: span 3;
        }
        
        &:nth-child(3) {
            grid-column: span 2;
        }

        &:nth-child(4) { 
            grid-column: span 3;
        }

        &#naranja
        {
            background-color: rgb(255, 128, 0);
        }
    }
}



